<template>
  <table
    class="goods-style"
    border=1
    cellspacing=0
    cellpadding=3
  >
    <thead>
      <tr>
        <th v-for="displayName in msg.displayNames">{{displayName}}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(row,i) in msg.data">
        <td v-for="(count,j) in row">
          <template v-if="j!=0 && j!=row.length-1">
            <input
              class="sum"
              v-on:blur="onBlur(i,j)"
              :placeholder=count
              v-model="bindData[i][j-1]"
              v-on:keydown="onChange(i,j,$event)"
              v-on:keyup="onKeyup"
              v-on:paste="paste"
            >
          </template>
          <template v-else-if="!j">
            {{count}}
          </template>
          <template v-else>
            {{bindData[i][j-1]}}
          </template>
        </td>
      </tr>
    </tbody>
    <tfoot>
      <tr>
        <td colspan="5">
          <span>总件数：{{totalCount}}，总金额：¥{{totalPrice}}。</span>
          <button
            class="submit"
            :disabled="totalCount == 0"
          >提交订单</button>
        </td>
      </tr>
    </tfoot>
  </table>
</template>

<script>
export default {
  name: "GoodsStyle",
  props: {
    msg: Object,
    price: Number
  },
  data: function() {
    return {
      totalPrice: 0,
      totalCount: 0,
      totalPrice: 0,
      bindData: []
    };
  },
  created() {
    this.msg.data.forEach((val, index) => {
      this.bindData[index] = new Array(4).fill("");
      this.bindData[index][3] = 0;
    });
  },
  computed: {},
  methods: {
    getCount: function(index) {
      let count = 0;
      const rowData = this.bindData[index];
      for (let i = 0, len = rowData.length; i < len; i++) {
        count += rowData[i] ? parseInt(rowData[i]) : 0;
      }
      return count;
    },
    onBlur: function(index, index2) {
      const rowData = this.bindData[index];
      const kucun = this.msg.data[index][index2];
      if (kucun < rowData[index2 - 1]) {
        alert(`超过库存数量${kucun}`);
        rowData[index2 - 1] = kucun;
        window.event.target.focus();
      }
      let count = 0;
      const len = rowData.length - 1;
      for (let i = 0; i < len; i++) {
        count += rowData[i] ? parseInt(rowData[i]) : 0;
      }
      rowData[len] = count;
      this.$set(this.bindData, index, rowData);
      this.getTotal();
    },
    getTotal() {
      let totalCount = 0;
      const datasource = this.bindData;
      for (let i = 0, len1 = datasource.length; i < len1; i++) {
        totalCount += datasource[i][3];
      }
      this.totalCount = totalCount;
      this.totalPrice = (totalCount * this.price).toFixed();
    },
    onChange(i, j, e) {
      const code = e.keyCode;
      if (e.key) {
        if (![37,39,8,9].includes(code) && !(/\d/.test(e.key))) {
          e.preventDefault();
        }
      }
      if (code === 13) {
        this.onBlur(i, j);
      }
    },
    onKeyup(e) {
      const target = e.target || e.srcElement;
      target.value = target.value.replace(/[^\d|\.]/g, "");
    },
    paste() {
      return false;
    }
  }
};
</script>

